import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useRouter } from 'next/router';
import {
    fetchProductDetail,
    createCartForGuest,
    createCartForUser,
    addItemToCart,
    fetchReviewsByProduct,
    fetchAverageRating,
    createReview,
} from '../../store/slices';
import { getToken, getCartId } from '../../utils/storage';

import Layout from '../../components/Layout';
import ProductImages from './components/ProductImages';
import ProductDetails from './components/ProductDetails';
import ReviewsSection from './components/ReviewsSection';

export default function Slug() {
    const dispatch = useDispatch();
    const router = useRouter();
    const { slug } = router.query;

    const { currentProduct, loading: productLoading, error: productError } = useSelector((state) => state.products);
    const { reviews, averageRating, pagination } = useSelector((state) => state.reviews);

    const [selectedColor, setSelectedColor] = useState(null);
    const [selectedSize, setSelectedSize] = useState(null);
    const [quantity, setQuantity] = useState(1);

    useEffect(() => {
        if (slug) {
            dispatch(fetchProductDetail(slug));
        }
    }, [dispatch, slug]);

    useEffect(() => {
        if (currentProduct?.id) {
            dispatch(fetchReviewsByProduct({ productId: currentProduct.id, page: 1, limit: 5 }));
            dispatch(fetchAverageRating(currentProduct.id));
        }
    }, [dispatch, currentProduct]);

    useEffect(() => {
        if (currentProduct?.productColors?.length > 0) {
            setSelectedColor(currentProduct.productColors[0]);
        }
        if (currentProduct?.productSizes?.length > 0) {
            setSelectedSize(currentProduct.productSizes[0]);
        }
    }, [currentProduct]);

    const handleAddToCart = async () => {
        if (!selectedColor || !selectedSize) {
            alert('Please select a color and size.');
            return;
        }

        try {
            let activeCartId = getCartId();
            if (!activeCartId) {
                const cartResponse = getToken()
                    ? await dispatch(createCartForUser()).unwrap()
                    : await dispatch(createCartForGuest()).unwrap();
                activeCartId = cartResponse.id;
            }

            const cartItemData = {
                cart_id: activeCartId,
                product_id: currentProduct?.id,
                color_id: selectedColor?.id,
                size_id: selectedSize?.id,
                quantity,
            };

            await dispatch(addItemToCart({ cartId: activeCartId, itemData: cartItemData })).unwrap();
            alert('Item added to cart successfully!');
        } catch (error) {
            console.error('Failed to add item to cart:', error);
            alert('Failed to add item to cart.');
        }
    };

    const handleSubmitReview = async (reviewData) => {
        try {
            await dispatch(createReview(reviewData)).unwrap();
            dispatch(fetchReviewsByProduct({ productId: currentProduct.id, page: pagination.currentPage, limit: 5 }));
            dispatch(fetchAverageRating(currentProduct.id));
        } catch (error) {
            console.error('Failed to submit review:', error);
        }
    };

    const handlePageChange = (newPage) => {
        if (currentProduct?.id && newPage !== pagination.currentPage) {
            dispatch(fetchReviewsByProduct({ productId: currentProduct.id, page: newPage, limit: 5 }));
        }
    };

    if (productLoading) return <div className="text-center py-10 text-xl">Loading product details...</div>;
    if (productError) return <div className="text-center text-red-500 py-10">{productError}</div>;
    if (!currentProduct) return <div className="text-center py-10 text-xl">Product not found</div>;

    return (
        <Layout>
            <div className="container mx-auto px-4 py-6">
                <div className="flex flex-col md:flex-row gap-6">
                    <ProductImages
                        productColors={currentProduct.productColors}
                        selectedColor={selectedColor}
                        setSelectedColor={setSelectedColor}
                    />
                    <div className="hidden md:block border-l border-gray-300"></div>
                    <ProductDetails
                        product={currentProduct}
                        selectedColor={selectedColor}
                        selectedSize={selectedSize}
                        setSelectedSize={setSelectedSize}
                        quantity={quantity}
                        setQuantity={setQuantity}
                        handleAddToCart={handleAddToCart}
                    />
                </div>
                <div className="mt-8">
                    <ReviewsSection
                        reviews={reviews}
                        averageRating={averageRating}
                        pagination={pagination}
                        handlePageChange={handlePageChange}
                        handleSubmitReview={handleSubmitReview}
                    />
                </div>
            </div>
        </Layout>
    );
}
